<style lang="less">
@largeWidth: 374px;
@midWidth: 555px;
@midHeight: 210px;
@smallWidth: 210px;

.show-box{
  position: relative;
  height: @largeWidth + @smallWidth;
  width: @largeWidth + @midWidth + 2 * @smallWidth;
  .new-goods {
    position: absolute;
    width: 578px;
    height: 161px;
    top: @smallWidth;
    left: @largeWidth;
    text-align: center;
    line-height: 161px;
    font-size: 24px;
  }
  .section{
    position: absolute;
    box-sizing: border-box;
    .pic{
      width: 100%;
      height: 100%;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .mask{
      opacity: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      background: rgba(0, 0, 0, .5);
      &:hover {
        opacity: 1;
        cursor: pointer;
      }
      &:hover span {
        color: #fff;
      }
    }
    .info {
      position: absolute;
      top: 0;
      cursor: pointer;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      span{
        display: block; 
        margin-bottom: 10px;
      }
    }

    &:nth-child(1){
      width: @largeWidth;
      height: @largeWidth;
    }
    &:nth-child(2){
      width: @smallWidth;
      height: @smallWidth;
      left: @largeWidth;
    }
    &:nth-child(3){
      width: @midWidth;
      height: @midHeight;
      left: @largeWidth + @smallWidth;
    }
    &:nth-child(4){
      width: @smallWidth;
      height: @smallWidth;
      right: 0;
    }
    &:nth-child(5){
      width: @largeWidth;
      height: @largeWidth;
      top: @smallWidth;
      right: 0;
    }
    &:nth-child(6){
      width: @smallWidth;
      height: @smallWidth;
      bottom: 0;
      right: @largeWidth;
    }
    &:nth-child(7){
      width: @midWidth;
      height: @midHeight;
      left: @smallWidth;
      bottom: 0;
    }
    &:nth-child(8){
      width: @smallWidth;
      height: @smallWidth;
      left: 0;
      bottom: 0;
    }
  }

}
</style>



<template>
  <section class="show-box">
    <div class="section" v-for="item in boxs" v-if="boxfetchState==='resolve'">
      <div class="pic">
        <img v-bind:src="item.informationimage">
      </div>
      <div class="mask" v-link="{ path: 'detail/' + item.informationid}">
        <div class="info">
          <div>
            <span>[{{getGoodsType(item.informationtypeid)}}]</span>
            <span>{{item.title}}</span>
            <span>标价:{{item.exprice}}元</span>
          </div>
        </div>
      </div>
    </div>
    <a class="new-goods" v-link="{ name: 'search'}">
        点击浏览最新失物信息
    </a>
  </section>
</template>


<script>
export default{
  data() {
    return {
      boxfetchState: 'pending',
      boxs: []
    }
  },
  ready() {
    this.fetchboxInfo();
  },
  methods: {
    getGoodsType(typeId) {
        switch(typeId) {
          case 1: return '入手求购';
          case 2: return '出手转让';
          case 3: return '物品租赁';
          case 4: return '失物招领';
          default: return '未知';
        }
    },
    fetchboxInfo() {
       setTimeout(() => {
            let boxsList = require('../../../mock/boxsList'); //请求
            boxsList = boxsList.default;
            this.boxs = boxsList.data;
            this.boxfetchState = 'resolve';
       }, 1000);
    }
  }
}


</script>



