<style lang="less">
@itemHeight: 50px;
@bgColor: #7fd0c0;
@atSize: 6px;
.menus_wrap{
	width: 210px;
	height: 410px;
	position: absolute;
	top: 120px;
}
.menus{
		position: absolute;
		width: 100px;
		top: 0;
		text-align: center;

		li{
			margin-bottom: 10px;
			cursor: pointer;
		}

		&_firstcate{
			left: 0;
			a.active:after{
				border-left: @atSize solid @bgColor;
				right: -@atSize;
			}
		}
		&_secondcate{
			left: 110px;
			top: 50%;
	    	transform: translateY(-50%);
			li {
				margin-bottom: 0;
				border-bottom: 1px solid #fff;
			}
		}

		&_type{
			right: 0;
		}

		a{
			position: relative;
			display: block;
			height: @itemHeight;
			line-height: @itemHeight;
			color: #fff;
			background-color: @bgColor;

			&.active:after{
				position: absolute;
				top: 0;
				bottom: 0;
				margin: auto;
				content: '';
				display: block;
				width: 0;
				height: 0;
				border-top: @atSize solid transparent;
				border-bottom: @atSize solid transparent;
			}
		}
	}

</style>

<template>
<ul class="menus menus_firstcate">
	<li>
		<a @click="sendCateId(-1)" :class="{'active': curCateIdx === -1}">最新</a>
	</li>
	<li>
		<a @click="sendCateId(-2)" :class="{'active': curCateIdx === -2}">最热</a>
	</li>
</ul>
<div class="menus_wrap" @mouseleave="changeSecCateShow(false, -3)">
	<ul class="menus menus_firstcate" >
		<li v-for="its in firstCategory" @mouseover="changeSecCateShow(true, $index)">
			<a v-text="its.informationname" @click="sendCateId(its.informationcategoryid)"></a>
		</li>
	</ul>
	<ul class="menus menus_secondcate" v-show="catesFetchState==='resolve' && secCateShow" >
		<li v-for="its in secondCategory[curCateIdx] ? secondCategory[curCateIdx].childrenCates : []" >
			<a v-text="its.informationname" @click="sendCateId(its.informationcategoryid)"></a>
		</li>
	</ul>
</div>
<slot></slot>
<ul class="menus menus_type">
	<li v-for="its in infoTypes">
		<a v-text="its.informationtypename" @click="sendTypeId(its.informationtypeid)"></a>
	</li>
</ul>
</template>

<script>
export default {
	data() {
		return {
			catesFetchState: 'pending',
			curCateIdx: -1, //当前分类索引 -1表示最新，-2表示最热，-3表示什么都不是，0表示电脑手机
			secCateShow: false, //二级页面是否展开
			firstCategory : [],
			secondCategory : [],
			infoTypes: []
		}
	},
	props: [],
	ready() {
        this.fetchGoodsCategories();
    },
    methods: {
		fetchGoodsCategories() {
			setTimeout(() => {
                let categories = require('../../mock/categories'); //请求
                categories = categories.default;
                const firstCates = categories.firstCategories.data,
                	secondCates = categories.secondCategories.data,
                	infoTypes = categories.infoTypes.data;
                this.firstCategory = firstCates;
                this.secondCategory = secondCates;
                this.infoTypes = infoTypes;
                this.catesFetchState = 'resolve';
            }, 1000);
		},
		changeSecCateShow(showState, idx) {
			this.curCateIdx = idx;
			this.secCateShow=showState;
		},
		sendCateId(id) {
			if (id === -1 || id === -2) {
				this.curCateIdx = id;
			}
			this.$emit('cateid', id);
		},
		sendTypeId(id) {
			this.$emit('typeid', id);
		}
    }
}
</script>


