<style lang="less">
@borderColor: #eee;
@itemWidth: 350px;
@titleHeight: 50px;
@avatarSize: 60px;
@color: #FFC124;
@activeColor: #fff;
@page_margin: 30px;
.pages_box{
    text-align: center;
    margin: 2*@page_margin 0 @page_margin 0;
    li{
        display: inline-block;
        background-color: #FFF;
        margin: 0 15px;
        a{
            display: inline-block;
            padding: 10px;
            &.active{
                color: @activeColor;
                background: @color;
                border: solid 1px @color;
            }
            &.unactive{
                color: @color;
                border: solid 1px @color;
            }
            &.unable{
                color: #fff;
                background: #ddd;
                border-width: solid 1px #ddd;
            }
        }
    }
    .hidden{
      display: none;
    }
}
.border-right{
	border-right: 1px solid @borderColor;
}
.goods_box{
	width: 800px;
	margin: 0 auto;
	-webkit-flex-wrap: wrap;
	flex-wrap: wrap;
	-webkit-justify-content: space-between;
	justify-content: space-between;
    .loading,
    .reject{
        width: 100%;
        height: 500px;
        text-align: center;
    }
	.item{
		width: @itemWidth;
		margin-bottom: 20px;
		background-color: #fff;
		border: 1px solid @borderColor;

		&_img{
			padding: 30px @titleHeight 0;
			img{
				border: 1px solid #eee;
				padding: 3px;
				border-radius: 3px;
			}
		}

		img{
			width: 100%;
			vertical-align: middle;
		}

		&_title{
			height: @titleHeight;
			line-height: @titleHeight;
			text-align:center;
			overflow: hidden;
		}

		.info{
			color: #333;
			border-top: 1px solid @borderColor;

			&_avatar{
				width: @avatarSize;
				padding: 10px;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;
			}

			&_name{
				width: 80px;
				line-height: 60px;
				font-size: 12px;
				padding: 0 5px;
				overflow: hidden;
				box-sizing: border-box;
			}

			&_pos{
				width: 100px;
				line-height: @avatarSize / 2;
				text-align: center;
			}

			&_price{
				line-height: @avatarSize / 2;
				text-align: center;
			}
		}
	}
}
</style>

<template>
    <section class="goods_box flex-wrap">
    	<div class="item" v-for="its in curgoods" v-if="curfetchgoodsstate==='resolve'">
    			<a v-link="{ path: 'detail/' + its.informationid}">
    				<div class="item_img">
    					<img v-bind:src="its.informationimage" alt="">
    				</div>
    				<h3 class="item_title">{{its.title}}</h3>
    			</a>
    			<div class="info flex-wrap">
    				<div class="info_avatar">
    					<img v-bind:src="its.userimg" alt="">
    				</div>
    				<div class="info_name">
    					<p>{{its.username}}</p>
    				</div>
    				<div class="info_pos">
    					<p>{{its.address}}</p>
    					<p>{{its.isbusiness ? '可校外交易' : '仅校内交易'}}</p>
    				</div>
    				<div class="info_price flex-con">
    					<p>￥{{its.exprice}}</p>
    					<p>{{its.isbargain ? '可议价' : '不议价'}}</p>
    				</div>
    			</div>
    	</div>
        <div v-show="curfetchgoodsstate=='pending'" class="loading">
            正在加载数据中...
        </div>
        <div v-show="curfetchgoodsstate=='reject'" class="reject">
            数据加载失败！检查您的网络...
        </div>
    </section>
        <ul class="pages_box" >
            <li v-for="index in pages" @click="gotoPage(index)" class="item">
              <a href="javascript:void(0)" :class="[current == index ? 'active' : 'unactive']">{{index}}</a>
            </li>
            <li v-show="current != 1" @click="current-- && gotoPage(current)" class="item">
                <a href="javascript:void(0)" class="unactive">上一页</a>
            </li>
            <li v-show="allpage != current && allpage != 0 " @click="current++ && gotoPage(current++)" :class="[current==curallpages ? 'hidden' : 'item']">
                <a href="javascript:void(0)" class="unactive">下一页</a>
            </li>
        </ul>
</template>

<script>
export default {
	data() {
		return {
            pages: [],
            current:1,
            showItem:5
		}
	},
    props: ['curcateid', 'curgoods', 'curfetchgoodsstate', 'curallpages'],
    computed:{
          pages:function(){
                var pag = [];
                  if( this.current < this.showItem ){ //如果当前的激活的项 小于要显示的条数
                       //总页数和要显示的条数那个大就显示多少条
                       var i = Math.min(this.showItem,this.curallpages);
                       while(i){
                           pag.unshift(i--);
                       }
                   }else{ //当前页数大于显示页数了
                       var middle = this.current - Math.floor(this.showItem / 2 ),//从哪里开始
                           i = this.showItem;
                       if( middle >  (this.curallpages - this.showItem)  ){
                           middle = (this.curallpages - this.showItem) + 1
                       }
                       while(i--){
                           pag.push( middle++ );
                       }
                   }
                 return pag
               }
      },
    methods: {
        gotoPage:function(index){ //
          if(index == this.current) return;
            this.current = index;
            //这里可以发送ajax请求  按页码发请求
            this.$emit('pagenumber', index);
        }
    }
}
</script>



