<template>
	<ui-show></ui-show>
	<div class="bd_main">
		<ui-aside-menu @cateid="getCateId" @typeid="getTypeId">
			<ui-goods :curgoods="curGoods"
					  :curfetchgoodsstate="curFetchGoodsState"
					  :curallpages="curAllPages"
					  @pagenumber="getPageGoods"></ui-goods>
		</ui-aside-menu>
		<ui-Official></ui-Official>
	</div>
</template>

<script>
import uiShow from '../show'
import uiAsideMenu from '../../../components/asideMenu'
import uiGoods from '../goods'
import uiOfficial from '../../../components/official'

//父组件
export default {
	data() {
		return {
			curFetchGoodsState: 'pending',
			curCateId: -1,
			curTypeId: -1,
			curGoods: [],
			curAllPages: 0
		}
	},
	ready() {
        this.getCateId(-1); //-1表示最新商品分类id
    },
	methods: {
		//请求对应分类商品总数量和请求对应类型商品总数量应为同一接口
		fetchGoodsCounts(cateId) {
            setTimeout(() => {
                let goodsCounts = require('../../../../mock/newgoodsCounts'); //请求
                goodsCounts = goodsCounts.default;
                this.curAllPages = Math.floor(goodsCounts/8) + ( goodsCounts%8 ? 1 : 0 );
            }, 1000);
        },
        //按页面请求对应分类商品和请求对应类型商品应为同一接口
        fetchGoodsInfo(cateId, page) {
            setTimeout(() => {
                let goodslist = require('../../../../mock/newgoodslist'); //假装请求最新商品
                goodslist = goodslist.default;
                this.curGoods = goodslist.data.recordList;
                this.curFetchGoodsState = 'resolve';
            }, 1000);
        },
        //按不同商品信息分类请求对应商品
		getCateId(id) {
			this.curCateId = id;
			this.curFetchGoodsState = 'pending';
			this.fetchGoodsCounts(id);
			this.fetchGoodsInfo(id, 1); //默认请求第一页
		},
		//按不同信息类型请求对应商品
		getTypeId(id) { 
			this.curTypeId = id;
			this.curFetchGoodsState = 'pending';
			this.fetchGoodsCounts(id);
			this.fetchGoodsInfo(id, 1); //默认请求第一页
		},
		//按页请求
		getPageGoods(page) {
			this.curFetchGoodsState = 'pending';
			this.fetchGoodsInfo(this.curCateId, page);
		}
	},
	components: {
		uiShow,
		uiAsideMenu,
		uiGoods,
		uiOfficial
	}
}
</script>


