<style lang="less">
@itemHeight: 50px;
@bgColor: #7fd0c0;
@atSize: 6px;

.official{
	position: absolute;
	width: 100px;
	text-align: center;
	li{
		margin-bottom: 10px;
	}
	&_about{
		right: 0;
		bottom: 0;
		li{
			margin-bottom: 0;
			a{
				background-color: #C8C8C8;
			}
			a:hover{
				background-color: @bgColor;
			}
		}
	}
	a{
		position: relative;
		display: block;
		height: @itemHeight;
		line-height: @itemHeight;
		color: #fff;
		background-color: @bgColor;

		&.active:after{
			position: absolute;
			top: 0;
			bottom: 0;
			margin: auto;
			content: '';
			display: block;
			width: 0;
			height: 0;
			border-top: @atSize solid transparent;
			border-bottom: @atSize solid transparent;
		}
	}
}
</style>

<template>
<ul class="official official_about">
	<li>
		<a href="http://weibo.com/creatshare?sudaref=www.baidu.com&retcode=6102&is_all=1" target="_blank">官方微博</a>
	</li>
	<li>
		<a v-link="{name: 'advice' }" target="_blank">意见反馈</a>
	</li>
	<li>
		<a v-link="{name: 'about' }" target="_blank">关于</a>
	</li>
	<li>
		<a v-link="{name: 'contact' }" target="_blank">联系我们</a>
	</li>
	<li>
		<a href="#" v-on:click.prevent="toTop()">^</a>
	</li>
</ul>
</template>

<script>
export default {
	methods: {
		//回到顶部
		toTop() {
			function step() {
				document.body.scrollTop -= 100;
			    if (document.body.scrollTop > 0) {
			        requestAnimationFrame(step);
			    }
			}
			requestAnimationFrame(step);
		}
	}
}
</script>


